import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Auth } from '../services/AuthService';

const HebNavbar = () => {
    const [navConfig, setNavConfig] = useState({ links: [], isAuthenticated: false });
    const [error, setError] = useState(null);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [coinsValue, setCoinsValue] = useState('');
    const location = useLocation();

    useEffect(() => {
        const checkAuth = async () => {
            const [loggedIn] = await Auth.isLoggedIn();
            setIsLoggedIn(loggedIn);
        };
        checkAuth();
    }, []);

    useEffect(() => {
        const fetchCoins = async () => {
            if (isLoggedIn) {
                try {
                    const token = document.cookie
                        .split(';')
                        .find(c => c.trim().startsWith('frontend_token='))
                        ?.split('=')[1];

                    if (!token) return;

                    const [, userId] = await Auth.isLoggedIn();
                    if (!userId) return;

                    const response = await fetch(`/api/user-coins-balance?user_id=${userId}`, {
                        headers: {
                            'Authorization': `Bearer ${token}`,
                            'Accept': 'application/json'
                        }
                    });

                    if (!response.ok) throw new Error('Failed to fetch coins balance');
                    const data = await response.json();
                    setCoinsValue(data.current_coins_balance);
                } catch (err) {
                    console.error('Failed to fetch coins:', err);
                }
            }
        };

        fetchCoins();
    }, [isLoggedIn]);

    useEffect(() => {
        const fetchNavConfig = async () => {
            try {
                const response = await fetch('/api/nav-config?is_hebrew=true', {
                    credentials: 'include',
                    headers: { 'Accept': 'application/json' }
                });
                if (!response.ok) throw new Error('Failed to fetch nav config');
                const data = await response.json();
                setNavConfig(data);
                setError(null);
            } catch (err) {
                console.error('Failed to fetch nav config:', err);
                setError('Failed to load navigation');
            }
        };

        fetchNavConfig();
    }, [isLoggedIn]);

    const handleLogout = async () => {
        try {
            await Auth.logout();
            setIsLoggedIn(false);
            window.location.reload();
        } catch (err) {
            console.error('Logout failed:', err);
        }
    };

    const handleCoinClick = () => {
        if (!isLoggedIn) {
            window.location.href = `/auth/login-heb?next_url=${encodeURIComponent(location.pathname)}`;
            return;
        }

        const coin = document.getElementById('nav-coin');
        if (coin && !coin.classList.contains('spin')) {
            coin.classList.add('spin');
            setTimeout(() => {
                coin.classList.remove('spin');
            }, 600);
        }
    };

    const handleLinkPath = (link) => {
        if (link.path.startsWith('http')) {
            return link.path;
        }
        return link.path;
    };

    const renderLinkContent = (link) => {
        if (link.type === 'logo') {
            return (
                <div
                    dangerouslySetInnerHTML={{ __html: link.text }}
                    style={{ height: '30px', width: 'auto' }}
                />
            );
        }
        return link.text;
    };

    const renderDropdown = (link) => (
        <div className="relative group">
            <button className="flex items-center text-gray-900 hover:text-[#000080] whitespace-nowrap focus:outline-none">
                {link.text}
                <svg
                    className="mr-2 w-4 h-4"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        fillRule="evenodd"
                        d="M5.23 7.21a.75.75 0 011.06.02L10 10.94l3.71-3.71a.75.75 0 111.06 1.06l-4.24 4.24a.75.75 0 01-1.06 0L5.21 8.29a.75.75 0 01.02-1.08z"
                        clipRule="evenodd"
                    />
                </svg>
            </button>
            <div className="absolute right-0 top-full bg-white text-gray-900 rounded shadow-lg hidden group-hover:block min-w-[8rem] z-50">
                {link.children.map((child) => (
                    <a
                        key={child.path}
                        href={handleLinkPath(child)}
                        className="block px-4 py-2 hover:bg-gray-200 whitespace-nowrap text-right"
                    >
                        {child.text}
                    </a>
                ))}
            </div>
        </div>
    );

    return (
        <nav className="bg-white w-full border-b border-gray-200" dir="rtl">
            <div className="w-full px-[50px]">
                <div className="flex items-center h-[99px] py-3">
                    {/* Right section with navigation links */}
                    <div className="flex items-center space-x-12 space-x-reverse">
                        {navConfig.links
                            .filter(link => link.public || isLoggedIn)
                            .map((link) => {
                                const isActive = location.pathname === link.path;

                                if (link.children) {
                                    return (
                                        <div key={link.text} className="relative">
                                            {renderDropdown(link)}
                                        </div>
                                    );
                                }

                                return (
                                    <a
                                        key={link.path}
                                        href={handleLinkPath(link)}
                                        className={`text-[15px] font-medium tracking-wide hover:text-[#000080] whitespace-nowrap transition-colors
                                                ${isActive ? 'text-[#000080] border-b-2 border-[#000080]' : 'text-[#4a4a4a]'}`}
                                        target={link.path.startsWith('http') ? '_blank' : undefined}
                                        rel={link.path.startsWith('http') ? 'noopener noreferrer' : undefined}
                                    >
                                        {renderLinkContent(link)}
                                    </a>
                                );
                            })}
                    </div>

                    {/* Left section with coin and auth button */}
                    <div className="flex items-center gap-6 mr-auto">
                        {isLoggedIn && (
                            <div id="coins-counter" className="font-bold text-gray-900 text-2xl">
                                {coinsValue}
                            </div>
                        )}
                        <div
                            id="coin-container"
                            className={`w-[50px] h-[50px] ${!isLoggedIn ? 'filter grayscale opacity-50' : ''}`}
                        >
                            <div
                                id="nav-coin"
                                onClick={handleCoinClick}
                                className="cursor-pointer w-full h-full"
                            >
                                <img
                                    src="/static/media/coins/coin_e_50.png"
                                    alt="Coin"
                                    className="w-full h-full object-contain"
                                />
                            </div>
                        </div>
                        <div>
                            {isLoggedIn ? (
                                <button
                                    onClick={handleLogout}
                                    className="bg-[#ff7f50] text-white px-6 py-2 rounded hover:bg-opacity-90 transition-colors whitespace-nowrap"
                                >
                                    התנתק
                                </button>
                            ) : (
                                <button
                                    onClick={() => window.location.href = `/auth/login-heb?next_url=${encodeURIComponent(location.pathname)}`}
                                    className="bg-[#ff7f50] text-white px-6 py-2 rounded hover:bg-opacity-90 transition-colors whitespace-nowrap"
                                >
                                    התחבר
                                </button>
                            )}
                        </div>
                    </div>
                </div>
            </div>
            {error && (
                <div className="bg-red-100 text-red-600 px-[50px] py-2 text-center absolute w-full">
                    {error}
                </div>
            )}
        </nav>
    );
};

export default HebNavbar;