// src/components/EnglishPortalSkeleton.jsx
import React from 'react';
import EngNavbarSkeleton from './EngNavbarSkeleton';

const EnglishPortalSkeleton = () => {
  return (
    <div className="min-h-screen bg-white">
      {/* Announcement Bar Skeleton */}
      <div className="w-full bg-[#000080] h-12 animate-pulse" />

      {/* Navbar Skeleton */}
      <nav className="bg-[#000080] text-white w-full">
        <div className="w-full px-6">
          <div className="flex items-center h-16">
            {/* Navigation links skeleton */}
            <div className="flex items-center space-x-12">
              {[1, 2, 3].map((i) => (
                <div key={i} className="h-4 bg-white/20 rounded animate-pulse w-24" />
              ))}
            </div>

            {/* User controls skeleton */}
            <div className="flex items-center gap-6 ml-auto">
              <div className="h-8 w-16 bg-white/20 rounded animate-pulse" />
              <div className="w-[50px] h-[50px] bg-white/20 rounded-full animate-pulse" />
              <div className="h-10 w-24 bg-[#ff7f50] rounded animate-pulse" />
            </div>
          </div>
        </div>
      </nav>

      {/* Filter Bar Skeleton */}
      <div className="w-full">
        <div className="nav-content filters-row">
          <div className="search-container">
            {/* Filter buttons skeleton */}
            <div className="device-selector">
              <div className="h-16 w-40 bg-gray-100 rounded-lg animate-pulse" />
            </div>
            <div className="device-selector">
              <div className="h-16 w-40 bg-gray-100 rounded-lg animate-pulse" />
            </div>
            <div className="device-selector">
              <div className="h-16 w-40 bg-gray-100 rounded-lg animate-pulse" />
            </div>
            {/* Search input skeleton */}
            <div className="search-input-container">
              <div className="h-16 w-96 bg-gray-100 rounded-lg animate-pulse" />
            </div>
          </div>
          <div className="right-controls-wrapper">
            <div className="right-controls">
              <div className="h-10 w-32 bg-gray-100 rounded animate-pulse" />
              <div className="h-10 w-24 bg-gray-100 rounded animate-pulse" />
              <div className="h-10 w-48 bg-gray-100 rounded animate-pulse" />
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 p-8">
        <div className="flex">
          {/* Left Sidebar Skeleton */}
          <div className="w-[300px] mr-8">
            {/* Hero Card Skeleton */}
            <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-4 mb-4">
              <div className="flex flex-col items-center">
                <div className="h-6 w-32 bg-gray-200 rounded animate-pulse mb-4" />
                <div className="h-[100px] w-[100px] bg-gray-200 rounded-full animate-pulse mb-4" />
                <div className="h-6 w-40 bg-gray-200 rounded animate-pulse" />
              </div>
            </div>

            {/* Skills Panel Skeleton */}
            <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-4">
              <div className="flex justify-between items-center mb-4">
                <div className="h-8 w-24 bg-gray-200 rounded animate-pulse" />
              </div>
              <div className="space-y-2">
                {[1, 2, 3, 4, 5].map((i) => (
                  <div key={i} className="h-10 bg-gray-200 rounded-lg animate-pulse" />
                ))}
              </div>
            </div>
          </div>

          {/* Cards Grid Skeleton */}
          <div className="flex-1">
            <div
              className="grid gap-[30px] w-full"
              style={{
                gridTemplateColumns: 'repeat(auto-fill, 320px)',
                justifyContent: 'start'
              }}
            >
              {[1, 2, 3, 4, 5, 6].map((i) => (
                <div
                  key={i}
                  style={{
                    width: '320px',
                    justifySelf: 'center',
                    minHeight: '250px'
                  }}
                >
                  {/* Education Card Skeleton */}
                  <div className="h-[250px] bg-white rounded-lg shadow-sm overflow-hidden">
                    {/* Card header */}
                    <div className="h-[125px] bg-gray-200 animate-pulse" />
                    {/* Card body */}
                    <div className="p-5">
                      <div className="flex flex-wrap gap-2">
                        {[1, 2, 3].map((j) => (
                          <div key={j} className="h-6 w-20 bg-gray-200 rounded-lg animate-pulse" />
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EnglishPortalSkeleton;