// components/HebAnnouncementBar.jsx
import React, { useState, useEffect } from 'react';
import api from '../services/api';

const HebAnnouncementBar = () => {
  const [announcements, setAnnouncements] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchAnnouncements = async () => {
      try {
        const response = await api.get('/api/announcements', {
          params: { language_id: 1 }  // Hebrew
        });

        if (response.data?.status === 'success') {
          setAnnouncements(response.data.announcements);
        }
      } catch (err) {
        console.error('Failed to fetch announcements:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchAnnouncements();

    const refreshInterval = setInterval(fetchAnnouncements, 5 * 60 * 1000);
    return () => clearInterval(refreshInterval);
  }, []);

  useEffect(() => {
    if (announcements.length > 1) {
      const rotationTimer = setInterval(() => {
        setCurrentIndex(prevIndex =>
          prevIndex === announcements.length - 1 ? 0 : prevIndex + 1
        );
      }, 5000);

      return () => clearInterval(rotationTimer);
    }
  }, [announcements.length]);

  if (loading || announcements.length === 0) return null;

  const currentAnnouncement = announcements[currentIndex];

  return (
    <div
      className="h-[43px] bg-[#000080] flex items-center justify-center"
      dir="rtl"
    >
      {currentAnnouncement.link ? (
        <a
          href={currentAnnouncement.link}
          target="_blank"
          rel="noopener noreferrer"
          className="text-white text-sm hover:underline cursor-pointer"
        >
          {currentAnnouncement.message}
        </a>
      ) : (
        <span className="text-white text-sm">
          {currentAnnouncement.message}
        </span>
      )}
    </div>
  );
};

export default HebAnnouncementBar;