import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Auth } from '../services/AuthService';

const EngNavbar = () => {
    const [navConfig, setNavConfig] = useState({ links: [], isAuthenticated: false, styles: '' });
    const [error, setError] = useState(null);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [coinsValue, setCoinsValue] = useState('');
    const location = useLocation();
    const isHebrew = location.pathname.includes('-heb');

    const getAccessToken = () => {
        console.log('🔍 Checking cookies for tokens...');
        const cookies = document.cookie.split(';');

        // Try access_token first
        for (let cookie of cookies) {
            const [name, value] = cookie.trim().split('=');
            if (name === 'access_token') {
                return value;
            }
        }

        // Try frontend_token
        for (let cookie of cookies) {
            const [name, value] = cookie.trim().split('=');
            if (name === 'frontend_token') {
                return value;
            }
        }

        console.log('❌ No token found');
        return null;
    };

    useEffect(() => {
        const initializeNavbar = async () => {
            console.log('🚀 Starting navbar initialization...');
            const startTime = performance.now();

            try {
                // Get auth status and userId in one call
                const [loggedIn, userId] = await Auth.isLoggedIn();
                setIsLoggedIn(loggedIn);
                console.log(`⏱️ Auth check completed in ${(performance.now() - startTime).toFixed(2)}ms`);

                if (loggedIn && userId) {
                    const token = getAccessToken();

                    // Run API calls in parallel
                    const fetchStart = performance.now();
                    const [coinsResponse, navConfigResponse] = await Promise.all([
                        fetch(`/api/user-coins-balance?uid=${userId}`, {
                            method: 'GET',
                            headers: {
                                'Authorization': `Bearer ${token}`,
                                'Accept': 'application/json'
                            },
                            credentials: 'include'
                        }),
                        fetch(`/api/nav-config${isHebrew ? '?is_hebrew=true' : ''}`, {
                            credentials: 'include',
                            headers: { 'Accept': 'application/json' }
                        })
                    ]);
                    console.log(`⏱️ Parallel API calls completed in ${(performance.now() - fetchStart).toFixed(2)}ms`);

                    // Handle responses in parallel
                    const [coinsData, navData] = await Promise.all([
                        coinsResponse.ok ? coinsResponse.json() : null,
                        navConfigResponse.ok ? navConfigResponse.json() : null
                    ]);

                    if (coinsData) {
                        setCoinsValue(coinsData.current_coins_balance);
                    }

                    if (navData) {
                        setNavConfig(navData);
                    } else {
                        throw new Error('Failed to load navigation data');
                    }
                } else {
                    // If not logged in, just fetch nav config
                    const navResponse = await fetch(`/api/nav-config${isHebrew ? '?is_hebrew=true' : ''}`, {
                        credentials: 'include',
                        headers: { 'Accept': 'application/json' }
                    });

                    if (navResponse.ok) {
                        const navData = await navResponse.json();
                        setNavConfig(navData);
                    } else {
                        throw new Error('Failed to load navigation data');
                    }
                }

            } catch (err) {
                console.error('Initialization failed:', err);
                setError('Failed to load navigation');
            }

            const endTime = performance.now();
            console.log(`⏱️ Total navbar initialization took ${(endTime - startTime).toFixed(2)}ms`);
        };

        initializeNavbar();
    }, [isHebrew]);

    const handleLogout = async () => {
        try {
            await Auth.logout();
            setIsLoggedIn(false);
            window.location.reload();
        } catch (err) {
            console.error('Logout failed:', err);
        }
    };

    const handleLinkPath = (link) => {
        if (link.path.startsWith('http')) {
            return link.path;
        }
        return link.path;
    };

    const renderLinkContent = (link) => {
        if (link.type === 'logo') {
            return (
                <div
                    dangerouslySetInnerHTML={{ __html: link.text }}
                    style={{ height: '30px', width: 'auto' }}
                />
            );
        }
        return link.text;
    };

    const renderDropdown = (link) => (
        <div className="relative group">
            <button className="flex items-center text-gray-900 hover:text-[#000080] whitespace-nowrap focus:outline-none">
                {link.text}
                <svg
                    className={`ml-2 w-4 h-4 ${isHebrew ? 'mr-2' : ''}`}
                    fill="currentColor"
                    viewBox="0 0 20 20"
                >
                    <path
                        fillRule="evenodd"
                        d="M5.23 7.21a.75.75 0 011.06.02L10 10.94l3.71-3.71a.75.75 0 111.06 1.06l-4.24 4.24a.75.75 0 01-1.06 0L5.21 8.29a.75.75 0 01.02-1.08z"
                        clipRule="evenodd"
                    />
                </svg>
            </button>
            <div className="absolute left-0 top-full bg-white text-gray-900 rounded shadow-lg hidden group-hover:block min-w-[8rem] z-50">
                {link.children.map((child) => (
                    <a
                        key={child.path}
                        href={handleLinkPath(child)}
                        className="block px-4 py-2 hover:bg-gray-200 whitespace-nowrap"
                    >
                        {child.text}
                    </a>
                ))}
            </div>
        </div>
    );

    return (
        <nav className="bg-white w-full border-b border-gray-200" dir={isHebrew ? "rtl" : "ltr"}>
            <div className="w-full px-[50px]">
                <div className="flex items-center h-[99px] py-3">
                    {/* Navigation links section */}
                    <div className={`flex items-center ${isHebrew ? 'space-x-12 space-x-reverse' : 'space-x-12'}`}>
                        {navConfig.links
                            .filter(link => link.public || isLoggedIn)
                            .map((link) => {
                                const isActive = location.pathname === link.path;

                                if (link.children) {
                                    return (
                                        <div key={link.text} className="relative">
                                            {renderDropdown(link)}
                                        </div>
                                    );
                                }

                                return (
                                    <a
                                        key={link.path}
                                        href={handleLinkPath(link)}
                                        className={`text-[15px] font-medium tracking-wide hover:text-[#000080] whitespace-nowrap transition-colors
                                                ${isActive ? 'text-[#000080] border-b-2 border-[#000080]' : 'text-[#4a4a4a]'}`}
                                    >
                                        {renderLinkContent(link)}
                                    </a>
                                );
                            })}
                    </div>

                    {/* User controls section */}
                    <div className={`flex items-center gap-6 ${isHebrew ? 'mr-auto' : 'ml-auto'}`}>
                        {isLoggedIn && (
                            <div id="coins-counter" className="font-bold text-gray-900 text-2xl">
                                {coinsValue}
                            </div>
                        )}
                        <div
                            className={`w-[50px] h-[50px] ${!isLoggedIn ? 'filter grayscale opacity-50' : ''} cursor-pointer`}
                            onClick={() => {
                                if (!isLoggedIn) {
                                    window.location.href = `/auth/login?next_url=${encodeURIComponent(window.location.pathname)}`;
                                }
                            }}
                        >
                            <img
                                src="/static/media/coins/coin_e_50.png"
                                alt="Coin"
                                className="w-full h-full object-contain"
                            />
                        </div>
                        <div>
                            {isLoggedIn ? (
                                <button
                                    onClick={handleLogout}
                                    className="bg-[#ff7f50] text-white px-6 py-2 rounded hover:bg-opacity-90 transition-colors whitespace-nowrap"
                                >
                                    {isHebrew ? 'התנתק' : 'Logout'}
                                </button>
                            ) : (
                                <button
                                    onClick={() => window.location.href = `/auth/login?next_url=${encodeURIComponent(window.location.pathname)}`}
                                    className="bg-[#ff7f50] text-white px-6 py-2 rounded hover:bg-opacity-90 transition-colors whitespace-nowrap"
                                >
                                    {isHebrew ? 'התחבר' : 'Login'}
                                </button>
                            )}
                        </div>
                    </div>
                </div>
            </div>
            {error && (
                <div className="bg-red-100 text-red-600 px-[50px] py-2 text-center absolute w-full">
                    {error}
                </div>
            )}
        </nav>
    );
};

export default EngNavbar;