import React from 'react';
import '../styles/WelcomePopupHeb.css';

const WelcomePopup = ({ onClose }) => {
  return (
    <div className="welcome-popup-overlay">
      <div className="welcome-popup-container">
        {/* Left Box: Earn EZ Coins */}
        <div className="popup-side-box popup-coins-box">
          <img src="/static/media/coins/coin_e_50.png" alt="מטבעות EZ" />
          <h4>צברו מטבעות EZ!</h4>
          <p>
            כל תרגול שלכם, כל שאלה, מזכים אתכם במטבעות שתוכלו לרכוש איתם גיבורים חדשים — כי ללמוד יכול להיות כיף גדול!
          </p>
        </div>

        {/* Main Content */}
        <div className="popup-main-content">
          <img
            className="popup-hero-image"
            src="https://ez42.live/global/images/vxvxvsdas_character_12.png"
            alt="גיבור EZ"
          />
          <h2>ברוכים הבאים ל-EZ!</h2>
          <p>
            <strong>לא הגעתם עד לכאן רק כדי להגיע רק עד לכאן.</strong><br />
            ב-EZ, תרגול הופך למשחק—אנחנו כאן כדי לעזור לכם לשפר מיומנויות, לחזק את הביטחון העצמי, ולהצליח בכל מבחן שבדרך.
            מוכנים לאתגרים כיפיים שישדרגו את כל מה שלמדתם בבית הספר?
          </p>
        </div>

        {/* Right Box: Unlock Stage Heroes */}
        <div className="popup-side-box popup-heroes-box">
          <img src="https://ez42.live/global/images/45h4dfbd_character_5.png" alt="גיבורי הבמה" />
          <h4>הרוויחו גיבורי שלבים!</h4>
          <p>
            תעלו שלבים, ותרוויחו גיבורים חדשים שיצטרפו לניצחונות שלכם ויעודדו אתכם להמשיך לאתגר הבא!
          </p>
        </div>

        {/* Parents & Educators Section */}
        <div className="popup-parents-educators">
          <h3>הורים ומורים יקרים!</h3>
          <p>רוצים לדעת איך אנחנו יכולים לעזור? לחצו והיכנסו!</p>
          <div className="popup-parents-buttons">
            <button onClick={() => window.open('/pages/for-educators-heb', '_blank')}>
              מחנכים, בואו לפה
            </button>
            <button onClick={() => window.open('/pages/for-parents-heb', '_blank')}>
              הורים, אתם לפה
            </button>
          </div>
        </div>

        {/* CTA Button */}
        <div className="popup-cta">
          <button onClick={onClose}>יאללה, מתחילים לתרגל!</button>
        </div>
      </div>
    </div>
  );
};

export default WelcomePopup;
