import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, Navigate, useLocation } from 'react-router-dom';
import { CardProvider } from './context/CardContext';
import PortalDashboard from './components/PortalDashboard';
import HebPortalDashboard from './components/HebPortalDashboard';
import WelcomePopup from './components/WelcomePopup';
import HebWelcomePopup from './components/HebWelcomePopup'; // Import the Hebrew popup
import { Auth } from './services/AuthService';
import EnglishPortalSkeleton from './components/EnglishPortalSkeleton';
import HebrewPortalSkeleton from './components/HebrewPortalSkeleton';
import EngNavbarSkeleton from './components/EngNavbarSkeleton';

const Router = () => {
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [loading, setLoading] = useState(true);
    const [showPopup, setShowPopup] = useState(false);

    useEffect(() => {
        const checkAuth = async () => {
            try {
                const [loggedIn] = await Auth.isLoggedIn();
                setIsAuthenticated(loggedIn);
            } catch (error) {
                console.error("Error checking auth state:", error);
                setIsAuthenticated(false);
            } finally {
                setLoading(false);
            }
        };

        checkAuth();
    }, []);

    useEffect(() => {
        if (!loading && !isAuthenticated) {
            setShowPopup(true);
        } else {
            setShowPopup(false);
        }
    }, [isAuthenticated, loading]);

    const handleClosePopup = () => {
        setShowPopup(false);
    };

    if (loading) {
        // Check if we're on the Hebrew route
        const isHebrew = window.location.pathname.includes('/he');
        return isHebrew ? <HebrewPortalSkeleton /> : <EnglishPortalSkeleton />;
    }

    return (
        <BrowserRouter>
            <CardProvider>
                <Routes>
                    <Route
                        path="/"
                        element={
                            <>
                                {showPopup && <WelcomePopup onClose={handleClosePopup} />}
                                <PortalDashboard />
                            </>
                        }
                    />
                    <Route
                        path="/he/*"
                        element={
                            <>
                                {showPopup && <HebWelcomePopup onClose={handleClosePopup} />}
                                <HebPortalDashboard />
                            </>
                        }
                    />
                    <Route path="*" element={<Navigate to="/" replace />} />
                </Routes>
            </CardProvider>
        </BrowserRouter>
    );
};

export default Router;
