import React, { useState, useEffect } from 'react';
import '../styles/FilterBar.css';

const HebFilterBar = ({
    showFilters = false,
    selectedSubject,
    setSelectedSubject,
    selectedGrades = [],
    setSelectedGrades,
    searchQuery = '',
    setSearchQuery,
    selectedCategories = [],
    toggleCategory,
    selectedSections = [],
    toggleSection,
    clearAllFilters,
    cardsLength = 0,
    availableCategories = [],
    availableSections = [],
    availableGrades = []
}) => {
    const [activeDropdown, setActiveDropdown] = useState(null);
    const [localSearchValue, setLocalSearchValue] = useState(searchQuery);

    // Available subjects - can be expanded in the future
    const availableSubjects = [
        { id: 'English', name_eng: 'English', name_heb: 'אנגלית' },
        { id: 'Math', name_eng: 'Math', name_heb: 'מתמטיקה' }
    ];

    // Debounce for the search input
    useEffect(() => {
        const timer = setTimeout(() => {
            setSearchQuery(localSearchValue);
        }, 300);
        return () => clearTimeout(timer);
    }, [localSearchValue, setSearchQuery]);

    // Sync local input with external search query prop
    useEffect(() => {
        setLocalSearchValue(searchQuery || '');
    }, [searchQuery]);

    return (
        <div>
            {showFilters && (
                <>
                    <div className="nav-content filters-row" dir="rtl">
                        <div className="search-container">
                            {/* Subject Selector */}
                            <div
                                className={`device-selector ${activeDropdown === 'subject' ? 'active' : ''}`}
                                onClick={() => setActiveDropdown(activeDropdown === 'subject' ? null : 'subject')}
                            >
                                <div className="device-selector-content">
                                    <div className="filter-title">
                                        {availableSubjects.find(subject => subject.id === selectedSubject)?.name_heb}
                                    </div>
                                    <span className="device-selector-arrow flip-rtl">
                                        <svg viewBox="0 0 24 24">
                                            <path d="M7 10l5 5 5-5z"/>
                                        </svg>
                                    </span>
                                </div>
                                <div className="filter-dropdown">
                                    <div className="filter-dropdown-content">
                                        {availableSubjects.map(subject => (
                                            <div
                                                key={subject.id}
                                                className={`filter-option ${selectedSubject === subject.id ? 'selected' : ''}`}
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    setSelectedSubject(subject.id);
                                                }}
                                            >
                                                {subject.name_heb}
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>

                            {/* Category Selector */}
                            <div
                                className={`device-selector ${activeDropdown === 'category' ? 'active' : ''} ${
                                    selectedCategories.length > 0 ? 'has-selected' : ''
                                }`}
                                onClick={() =>
                                    setActiveDropdown(activeDropdown === 'category' ? null : 'category')
                                }
                            >
                                <div className="device-selector-content">
                                    <div className="filter-title">קטגוריות</div>
                                    <span className="device-selector-text">
                                        {selectedCategories.length > 0 && (
                                            <span className="selected-count">{selectedCategories.length}</span>
                                        )}
                                    </span>
                                    <span className="device-selector-arrow flip-rtl">
                                        <svg viewBox="0 0 24 24">
                                            <path d="M7 10l5 5 5-5z" />
                                        </svg>
                                    </span>
                                </div>
                                <div className="filter-dropdown">
                                    <div className="filter-dropdown-content">
                                        {availableCategories.map((category) => (
                                            <div
                                                key={category.id}
                                                className={`filter-option ${
                                                    selectedCategories.includes(category.id) ? 'selected' : ''
                                                }`}
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    toggleCategory(category.id);
                                                }}
                                            >
                                                {category.name_heb || category.name_eng}
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>

                            {/* Section Selector */}
                            <div
                                className={`device-selector ${activeDropdown === 'section' ? 'active' : ''} ${
                                    selectedSections.length > 0 ? 'has-selected' : ''
                                }`}
                                onClick={() =>
                                    setActiveDropdown(activeDropdown === 'section' ? null : 'section')
                                }
                            >
                                <div className="device-selector-content">
                                    <div className="filter-title">נושאים</div>
                                    <span className="device-selector-text">
                                        {selectedSections.length > 0 && (
                                            <span className="selected-count">{selectedSections.length}</span>
                                        )}
                                    </span>
                                    <span className="device-selector-arrow flip-rtl">
                                        <svg viewBox="0 0 24 24">
                                            <path d="M7 10l5 5 5-5z" />
                                        </svg>
                                    </span>
                                </div>
                                <div className="filter-dropdown">
                                    <div className="filter-dropdown-content">
                                        {availableSections.map((section) => (
                                            <div
                                                key={section.id}
                                                className={`filter-option ${
                                                    selectedSections.includes(section.id) ? 'selected' : ''
                                                }`}
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    toggleSection(section.id);
                                                }}
                                            >
                                                {section.name_heb || section.name_eng}
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>

                            {/* Grade Selector */}
                            <div
                                className={`device-selector ${activeDropdown === 'grade' ? 'active' : ''} ${
                                    selectedGrades.length > 0 ? 'has-selected' : ''
                                }`}
                                onClick={() =>
                                    setActiveDropdown(activeDropdown === 'grade' ? null : 'grade')
                                }
                            >
                                <div className="device-selector-content">
                                    <div className="filter-title">כיתות</div>
                                    <span className="device-selector-text">
                                        {selectedGrades.length > 0 && (
                                            <span className="selected-count">{selectedGrades.length}</span>
                                        )}
                                    </span>
                                    <span className="device-selector-arrow flip-rtl">
                                        <svg viewBox="0 0 24 24">
                                            <path d="M7 10l5 5 5-5z" />
                                        </svg>
                                    </span>
                                </div>
                                <div className="filter-dropdown">
                                    <div className="filter-dropdown-content">
                                        {availableGrades.map((grade) => (
                                            <div
                                                key={grade.id}
                                                className={`filter-option ${
                                                    selectedGrades.includes(grade.id) ? 'selected' : ''
                                                }`}
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    setSelectedGrades([grade.id]);
                                                }}
                                            >
                                                {grade.name_heb || grade.name_eng}
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>

                            <div className="search-input-container">
                                <span className="search-icon">
                                    <svg viewBox="0 0 24 24">
                                        <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z" />
                                    </svg>
                                </span>
                                <input
                                    type="text"
                                    placeholder="חיפוש דפים ומיומנויות..."
                                    value={localSearchValue}
                                    onChange={(e) => setLocalSearchValue(e.target.value)}
                                    className="search-input text-right"
                                    autoComplete="off"
                                    spellCheck="false"
                                    dir="rtl"
                                />
                            </div>
                        </div>

                        <div className="right-controls-wrapper">
                            <div className="right-controls">
                                {/* Counter Display */}
                                <div className="counter-display">
                                    {cardsLength} כרטיסים
                                </div>

                                {/* Clear Filters Button */}
                                <button
                                    onClick={clearAllFilters}
                                    className="action-button primary"
                                >
                                    נקה סינון
                                </button>
                            </div>
                        </div>

                        {/* Overlay for closing dropdowns */}
                        {activeDropdown && (
                            <div
                                className="dropdown-overlay active"
                                onClick={() => setActiveDropdown(null)}
                            />
                        )}
                    </div>

                    {/* Separate row for active filters */}
                    {(selectedCategories.length > 0 ||
                        selectedSections.length > 0 ||
                        selectedGrades.length > 0) && (
                        <div className="selected-filters-row" dir="rtl">
                            {availableCategories
                                .filter((cat) => selectedCategories.includes(cat.id))
                                .map((cat) => (
                                    <div
                                        key={`tag-cat-${cat.id}`}
                                        className="filter-tag"
                                        onClick={() => toggleCategory(cat.id)}
                                    >
                                        {cat.name_heb || cat.name_eng}
                                        <span className="remove">×</span>
                                    </div>
                                ))}
                            {availableSections
                                .filter((sec) => selectedSections.includes(sec.id))
                                .map((sec) => (
                                    <div
                                        key={`tag-sec-${sec.id}`}
                                        className="filter-tag"
                                        onClick={() => toggleSection(sec.id)}
                                    >
                                        {sec.name_heb || sec.name_eng}
                                        <span className="remove">×</span>
                                    </div>
                                ))}
                            {availableGrades
                                .filter((grade) => selectedGrades.includes(grade.id))
                                .map((grade) => (
                                    <div
                                        key={`tag-grade-${grade.id}`}
                                        className="filter-tag"
                                        onClick={() =>
                                            setSelectedGrades(
                                                selectedGrades.filter((id) => id !== grade.id)
                                            )
                                        }
                                    >
                                        {grade.name_heb || grade.name_eng}
                                        <span className="remove">×</span>
                                    </div>
                                ))}
                        </div>
                    )}
                </>
            )}
        </div>
    );
};

export default HebFilterBar;