import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';
import api from '../services/api';

const CardContext = createContext(null);

export function useCards() {
    const context = useContext(CardContext);
    if (!context) {
        throw new Error('useCards must be used within a CardProvider');
    }
    return context;
}

export function CardProvider({ children }) {
    // Core state
    const [cards, setCards] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    // Filter state
    const [selectedSubject, setSelectedSubject] = useState('English');
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedSkills, setSelectedSkills] = useState([]);
    const [selectedCategories, setSelectedCategories] = useState([]);
    const [selectedSections, setSelectedSections] = useState([]);
    const [selectedGrades, setSelectedGrades] = useState([]);

    // Cache version check
    const CACHE_VERSION = '2.0';
    const CACHE_KEY = `cards_${selectedSubject}_${CACHE_VERSION}`;

    // Fetch cards from API
    const fetchCards = useCallback(async () => {
        try {
            setLoading(true);

            // Check localStorage cache first
            const cachedData = localStorage.getItem(CACHE_KEY);
            if (cachedData) {
                const { data, timestamp, version } = JSON.parse(cachedData);
                const isCacheValid = version === CACHE_VERSION &&
                    (Date.now() - timestamp) < 5 * 60 * 1000; // 5 minutes cache

                if (isCacheValid) {
                    setCards(data);
                    setLoading(false);
                    return;
                }
            }

            // Fetch from API if cache miss or invalid
            const response = await api.get('/api/cards', {
                params: {
                    subject: selectedSubject,
                    language_id: 2,
                    include_hebrew: true
                }
            });

            // Cache the response
            localStorage.setItem(CACHE_KEY, JSON.stringify({
                data: response.data.cards,
                timestamp: Date.now(),
                version: CACHE_VERSION
            }));

            setCards(response.data.cards);
            setError(null);
        } catch (error) {
            console.error('Error fetching cards:', error);
            setError('Failed to fetch cards. Please try again later.');
            setCards([]);
        } finally {
            setLoading(false);
        }
    }, [selectedSubject, CACHE_KEY]);

    // Filter cards
    const filteredCards = React.useMemo(() => {
        return cards.filter(card => {
            // Search filter
            if (searchQuery) {
                const searchLower = searchQuery.toLowerCase();
                const titleMatch = (card.title?.toLowerCase().includes(searchLower) ||
                                  card.title_heb?.toLowerCase().includes(searchLower));
                const skillsMatch = card.skills?.some(skill =>
                    skill.skill_name_eng.toLowerCase().includes(searchLower) ||
                    skill.skill_name_heb?.toLowerCase().includes(searchLower)
                );
                if (!titleMatch && !skillsMatch) return false;
            }

            // Skills filter
            if (selectedSkills.length > 0) {
                if (!card.skills?.some(skill => selectedSkills.includes(skill.id))) {
                    return false;
                }
            }

            // Categories filter
            if (selectedCategories.length > 0) {
                const hasMatchingCategory = card.skills?.some(skill =>
                    skill.category && selectedCategories.includes(skill.category.id)
                );
                if (!hasMatchingCategory) {
                    return false;
                }
            }

            // Sections filter
            if (selectedSections.length > 0) {
                const hasMatchingSection = card.skills?.some(skill =>
                    skill.section && selectedSections.includes(skill.section.id)
                );
                if (!hasMatchingSection) {
                    return false;
                }
            }

            // Grades filter
            if (selectedGrades.length > 0) {
                if (!card.grades?.some(grade => selectedGrades.includes(grade.id))) {
                    return false;
                }
            }

            return true;
        });
    }, [cards, searchQuery, selectedSkills, selectedCategories, selectedSections, selectedGrades]);

    // Compute available filters based on filtered cards
    const availableFilters = React.useMemo(() => {
        const categories = new Set();
        const sections = new Set();
        const grades = new Set();

        filteredCards.forEach(card => {
            // Get categories from skills
            card.skills?.forEach(skill => {
                if (skill.category) {
                    categories.add(JSON.stringify({
                        id: skill.category.id,
                        name_eng: skill.category.name_eng,
                        name_heb: skill.category.name_heb,  // Add Hebrew name
                        category_name_eng: skill.category.category_name_eng,
                        category_name_heb: skill.category.category_name_heb  // Add Hebrew category name
                    }));
                }
                if (skill.section) {
                    sections.add(JSON.stringify({
                        id: skill.section.id,
                        name_eng: skill.section.name_eng,
                        name_heb: skill.section.name_heb,  // Add Hebrew name
                        section_name_eng: skill.section.section_name_eng,
                        section_name_heb: skill.section.section_name_heb,  // Add Hebrew section name
                        subject_category_id: skill.section.subject_category_id
                    }));
                }
            });

            // Get grades
            card.grades?.forEach(grade => {
                grades.add(JSON.stringify({
                    id: grade.id,
                    name_eng: grade.name_eng,
                    name_heb: grade.name_heb,  // Add Hebrew name
                    level_emoji: grade.level_emoji
                }));
            });
        });

        return {
            availableCategories: [...categories].map(cat => JSON.parse(cat))
                .sort((a, b) => (a.name_heb || a.name_eng).localeCompare(b.name_heb || b.name_eng)),
            availableSections: [...sections].map(sec => JSON.parse(sec))
                .sort((a, b) => (a.name_heb || a.name_eng).localeCompare(b.name_heb || b.name_eng)),
            availableGrades: [...grades].map(grade => JSON.parse(grade))
                .sort((a, b) => (a.name_heb || a.name_eng).localeCompare(b.name_heb || b.name_eng))
        };
    }, [filteredCards]);

    // Initial fetch
    useEffect(() => {
        fetchCards();
    }, [fetchCards]);

    const value = {
        // Data
        cards: filteredCards,
        loading,
        error,

        // Filters
        selectedSubject,
        setSelectedSubject,
        searchQuery,
        setSearchQuery,
        selectedSkills,
        setSelectedSkills,
        selectedCategories,
        setSelectedCategories,
        selectedSections,
        setSelectedSections,
        selectedGrades,
        setSelectedGrades,

        // Dynamic filter options
        availableCategories: availableFilters.availableCategories,
        availableSections: availableFilters.availableSections,
        availableGrades: availableFilters.availableGrades,

        // Actions
        refreshCards: fetchCards,

        // Clear filters
        clearFilters: () => {
            setSearchQuery('');
            setSelectedSkills([]);
            setSelectedCategories([]);
            setSelectedSections([]);
            setSelectedGrades([]);
        }
    };

    return (
        <CardContext.Provider value={value}>
            {children}
        </CardContext.Provider>
    );
}