import React, { useState, useRef, useEffect } from 'react';
import '../styles/EducationCard.css';

const EducationCard = ({
    title,
    skills = [],
    grades = [],
    requiresLogin = false,
    url,
    subject = 'English',
    colorDark = '#000080',
    isHebrew = false
}) => {
    const [isExpanded, setIsExpanded] = useState(false);
    const [visibleSkills, setVisibleSkills] = useState(skills);
    const [hiddenCount, setHiddenCount] = useState(0);
    const [visibleGradesCount, setVisibleGradesCount] = useState(0);
    const cardRef = useRef(null);
    const tagsContainerRef = useRef(null);
    const titleRef = useRef(null);

    useEffect(() => {
        if (!cardRef.current) return;
        const thisCard = cardRef.current;
        if (isExpanded) {
            const cards = document.querySelectorAll('.education-card');
            const thisRect = thisCard.getBoundingClientRect();
            const expandedHeight = thisCard.scrollHeight;
            cards.forEach((card) => {
                const rect = card.getBoundingClientRect();
                if (Math.abs(rect.top - thisRect.top) < 10) {
                    card.style.height = `${expandedHeight}px`;
                }
            });
        } else {
            const cards = document.querySelectorAll('.education-card');
            cards.forEach((card) => {
                card.style.height = '250px';
            });
        }
    }, [isExpanded]);

    useEffect(() => {
        function getTextWidth(text) {
            const canvas = document.createElement('canvas');
            const context = canvas.getContext('2d');
            context.font = '12px sans-serif';
            return context.measureText(text).width;
        }

        function updateVisibleTags() {
            if (!tagsContainerRef.current || !cardRef.current || !titleRef.current) return;
            if (isExpanded) {
                setVisibleSkills(skills);
                setHiddenCount(0);
                setVisibleGradesCount(grades.length);
                return;
            }

            const CARD_HEIGHT = 250;
            const HEADER_HEIGHT = 125;
            const PADDING = 20;
            const TAG_HEIGHT = 24;
            const TAG_MARGIN = 8;
            const BOTTOM_SPACE = 16;

            const availableSpace = CARD_HEIGHT - HEADER_HEIGHT - (PADDING * 2) - BOTTOM_SPACE;
            const maxRows = Math.floor(availableSpace / (TAG_HEIGHT + TAG_MARGIN));
            const containerWidth = tagsContainerRef.current.offsetWidth - (PADDING * 2);

            let currentRow = 0;
            let rowWidth = 0;
            let visibleSkillsCount = 0;
            let visibleGradesCount = 0;
            let plusTagWidth = 0;

            // Measure plus tag width
            const tempTag = document.createElement('span');
            tempTag.innerText = '+99';
            tempTag.className = 'inline-flex items-center px-2.5 py-1 rounded-lg text-xs font-medium';
            document.body.appendChild(tempTag);
            plusTagWidth = tempTag.offsetWidth + TAG_MARGIN;
            document.body.removeChild(tempTag);

            // Process skills first
            for (let i = 0; i < skills.length && currentRow < maxRows; i++) {
                const text = isHebrew ? skills[i].skill_name_heb : skills[i].skill_name_eng;
                const tagWidth = getTextWidth(text) + 40;

                if (rowWidth + tagWidth > containerWidth - (i < skills.length - 1 ? plusTagWidth : 0)) {
                    currentRow++;
                    rowWidth = tagWidth;
                    if (currentRow >= maxRows) break;
                } else {
                    rowWidth += tagWidth + TAG_MARGIN;
                }
                visibleSkillsCount++;
            }

            // Process grades with remaining space
            rowWidth = 0;
            if (currentRow < maxRows) {
                for (let i = 0; i < grades.length && currentRow < maxRows; i++) {
                    const text = isHebrew ? grades[i].name_heb : grades[i].name_eng;
                    const tagWidth = getTextWidth(text) + 40;

                    if (rowWidth + tagWidth > containerWidth - (i < grades.length - 1 ? plusTagWidth : 0)) {
                        currentRow++;
                        rowWidth = tagWidth;
                        if (currentRow >= maxRows - 1) break;
                    } else {
                        rowWidth += tagWidth + TAG_MARGIN;
                    }
                    visibleGradesCount++;
                }
            }

            visibleSkillsCount = Math.max(1, Math.min(visibleSkillsCount, skills.length));
            visibleGradesCount = Math.max(1, Math.min(visibleGradesCount, grades.length));

            if (currentRow < maxRows && visibleGradesCount < grades.length) {
                const remainingWidth = containerWidth - rowWidth;
                const nextGrade = grades[visibleGradesCount];
                if (nextGrade) {
                    const text = isHebrew ? nextGrade.name_heb : nextGrade.name_eng;
                    const tagWidth = getTextWidth(text) + 40;
                    if (tagWidth <= remainingWidth) {
                        visibleGradesCount++;
                    }
                }
            }

            setVisibleSkills(skills.slice(0, visibleSkillsCount));
            setHiddenCount(skills.length - visibleSkillsCount);
            setVisibleGradesCount(visibleGradesCount);
        }

        updateVisibleTags();
        window.addEventListener('resize', updateVisibleTags);
        return () => window.removeEventListener('resize', updateVisibleTags);
    }, [skills, grades, isExpanded, isHebrew]);

    const handleClick = () => {
        if (url) {
            window.open(url.startsWith('http') ? url : `/portal${url}`, '_blank');
        }
    };

    const handleExpandClick = (e) => {
        e.stopPropagation();
        setIsExpanded(!isExpanded);
    };

    return (
        <div
            ref={cardRef}
            className={`education-card bg-white rounded-lg shadow-sm relative overflow-hidden ${isExpanded ? 'h-auto' : 'h-[250px]'}`}
            dir={isHebrew ? 'rtl' : 'ltr'}
        >
            {/* Clickable top section with hover effects */}
            <div
                onClick={handleClick}
                className={`group h-[125px] flex items-center justify-center bg-cover bg-center bg-no-repeat relative rounded-t-lg 
                    ${subject.toLowerCase() === 'math' ? 'math-card-bg' : 'english-card-bg'}
                    ${url ? 'cursor-pointer hover:brightness-110 transition-all duration-200' : ''}`}
            >
                <div className="absolute inset-0" style={{backgroundColor: colorDark, opacity: 0.7}}></div>

                {/* Add click indicator */}
                {url && (
                    <div className="absolute top-2 right-2 text-white opacity-0 group-hover:opacity-100 transition-opacity duration-200">
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 6H6a2 2 0 00-2 2v10a2 2 0 002 2h10a2 2 0 002-2v-4M14 4h6m0 0v6m0-6L10 14" />
                        </svg>
                    </div>
                )}

                <h3 ref={titleRef} className="text-white text-xl font-semibold px-5 text-center relative z-10"
                    style={{fontSize: '1.6rem'}}>
                    {title}
                </h3>
            </div>

            <div className="p-5">
                <div
                    ref={tagsContainerRef}
                    className="flex flex-wrap gap-2"
                    onClick={handleExpandClick}
                    style={{cursor: 'pointer'}}
                >
                    {(isExpanded ? skills : visibleSkills).map((skill, index) => (
                        <span
                            key={index}
                            className="inline-flex items-center px-2.5 py-0.5 rounded-lg text-xs font-medium border bg-white mr-2 mb-2"
                            style={{borderColor: '#000080', color: '#000080'}}
                        >
                            {isHebrew ? skill.skill_name_heb : skill.skill_name_eng}
                        </span>
                    ))}
                    {!isExpanded && hiddenCount > 0 && (
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-lg text-xs font-medium bg-gray-100 text-gray-800 mr-2 mb-2">
                            +{hiddenCount}
                        </span>
                    )}

                    {grades.slice(0, isExpanded ? grades.length : visibleGradesCount).map((grade) => (
                        <span
                            key={grade.id}
                            className="inline-flex items-center px-2.5 py-0.5 rounded-lg text-xs font-medium border border-gray-300 bg-white text-gray-700 mr-2 mb-2"
                        >
                            {isHebrew ? grade.name_heb : grade.name_eng}
                        </span>
                    ))}
                    {!isExpanded && grades.length > visibleGradesCount && (
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-lg text-xs font-medium bg-gray-100 text-gray-800 mr-2 mb-2">
                            +{grades.length - visibleGradesCount}
                        </span>
                    )}
                </div>
            </div>
        </div>
    );
};

export default EducationCard;