import React, { useState, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import { useCards } from '../context/CardContext';
import EducationCard from './EducationCard';
import HebFilterBar from './HebFilterBar';
import UnifiedNavbar from './HebNavbar';
import api from '../services/api';
import HebAnnouncementBar from './HebAnnouncementBar';

const HebPortalDashboard = () => {
    const [searchParams, setSearchParams] = useSearchParams();

    // Get context values first
    const {
        cards,
        loading,
        error,
        selectedSubject,
        setSelectedSubject,
        searchQuery,
        setSearchQuery,
        selectedCategories,
        setSelectedCategories,
        selectedSections,
        setSelectedSections,
        selectedGrades,
        setSelectedGrades,
        selectedSkills,
        setSelectedSkills,
        clearFilters,
        availableCategories,
        availableSections,
        availableGrades
    } = useCards();

    // Local state
    const [skills, setSkills] = useState([]);
    const [categories, setCategories] = useState([]);
    const [sections, setSections] = useState([]);
    const [grades, setGrades] = useState([]);
    const [hero, setHero] = useState(null);

    // Fetch metadata (skills, categories, etc.)
    useEffect(() => {
        const fetchMetadata = async () => {
            try {
                // Fetch skills
                const skillsResponse = await api.get('/api/skills', {
                    params: {
                        subject: selectedSubject,
                        language: 'heb'
                    }
                });
                setSkills(skillsResponse.data.skills || []);

                // Fetch categories
                const categoriesResponse = await api.get('/api/categories', {
                    params: {
                        subject: selectedSubject,
                        language: 'heb'
                    }
                });
                setCategories(categoriesResponse.data.categories || []);

                // Fetch sections
                const sectionsResponse = await api.get('/api/sections', {
                    params: {
                        subject: selectedSubject,
                        language: 'heb'
                    }
                });
                setSections(sectionsResponse.data.sections || []);

                // Fetch grades
                const gradesResponse = await api.get('/api/grades', {
                    params: {
                        subject: selectedSubject,
                        language: 'heb'
                    }
                });

                // Transform grades to use Hebrew names
                const hebrewGrades = (gradesResponse.data.grades || []).map(grade => ({
                    ...grade,
                    name_eng: grade.name_heb // Use Hebrew name for display
                }));
                setGrades(hebrewGrades);

                // Fetch hero
                const heroResponse = await api.get('/api/stage-heroes', {
                    params: { language: 'heb' }
                });
                setHero(heroResponse.data);

            } catch (error) {
                console.error('Error fetching metadata:', error);
            }
        };

        fetchMetadata();
    }, [selectedSubject]);

    // Check URL parameter on mount and when it changes
    useEffect(() => {
        const subjectParam = searchParams.get('subject');
        if (subjectParam === 'Math' && selectedSubject !== 'Math') {
            setSelectedSubject('Math');
            clearFilters();
        }
    }, [searchParams, selectedSubject, clearFilters]);

    // Handle subject change
    const handleSubjectChange = (newSubject) => {
        setSearchParams({ subject: newSubject }, { replace: true });
        setSelectedSubject(newSubject);
        clearFilters();
    };

    return (
        <div className="min-h-screen bg-white" dir="rtl">
            <HebAnnouncementBar/>
            <div className="flex flex-col">
                <UnifiedNavbar/>
                <main>
                    <HebFilterBar
                        showFilters={true}
                        selectedSubject={selectedSubject}
                        setSelectedSubject={handleSubjectChange}
                        searchQuery={searchQuery}
                        setSearchQuery={setSearchQuery}
                        selectedCategories={selectedCategories}
                        toggleCategory={(id) => {
                            setSelectedCategories(prev =>
                                prev.includes(id)
                                    ? prev.filter(c => c !== id)
                                    : [...prev, id]
                            );
                        }}
                        selectedSections={selectedSections}
                        toggleSection={(id) => {
                            setSelectedSections(prev =>
                                prev.includes(id)
                                    ? prev.filter(s => s !== id)
                                    : [...prev, id]
                            );
                        }}
                        clearAllFilters={clearFilters}
                        cardsLength={cards.length}
                        availableCategories={availableCategories}
                        availableSections={availableSections}
                        grades={grades}
                        availableGrades={availableGrades}
                        selectedGrades={selectedGrades}
                        setSelectedGrades={setSelectedGrades}
                        isHebrew={true}
                    />

                    <div className="flex-1 p-8">
                        <div className="flex">
                            {/* Left Sidebar with Hero and Skills */}
                            <div className="w-[300px] ml-8">
                                {/* Hero Section */}
                                <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-4 mb-4">
                                    {hero ? (
                                        <div className="flex flex-col items-center">
                                            <div
                                                className="mt-2 text-center text-[15px] font-medium text-gray-900 mb-3">
                                                <div>פגוש את הגיבורים שלנו</div>
                                            </div>
                                            <img
                                                src={`data:image/jpeg;base64,${hero.base64}`}
                                                alt={hero.name_heb}
                                                className="h-[100px] object-contain"
                                            />
                                            <div className="mt-2 text-center text-[15px] font-medium text-gray-900">
                                                <div>{hero.name_heb}</div>
                                            </div>
                                        </div>
                                    ) : (
                                        <div className="flex flex-col items-center justify-center h-full">
                                            <div className="w-24 h-24 bg-gray-200 rounded-full animate-pulse"></div>
                                            <div className="mt-2 w-32 h-6 bg-gray-200 rounded animate-pulse"></div>
                                        </div>
                                    )}
                                </div>

                                {/* Skills Panel */}
                                <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
                                    <div className="px-6 py-4 border-b border-gray-200">
                                        <h2 className="text-[15px] font-medium text-gray-900">מיומנויות</h2>
                                    </div>
                                    <div className="px-4 py-4 space-y-1.5">
                                        {skills
                                            .sort((a, b) => a.skill_name_heb.localeCompare(b.skill_name_heb))
                                            .map((skill) => (
                                                <button
                                                    key={skill.id}
                                                    onClick={() => {
                                                        const newSkills = selectedSkills.includes(skill.id)
                                                            ? selectedSkills.filter(id => id !== skill.id)
                                                            : [...selectedSkills, skill.id];
                                                        setSelectedSkills(newSkills);
                                                    }}
                                                    className={`w-full text-right px-4 py-2.5 rounded-lg flex items-center justify-between group transition-colors mx-2
                                                    ${selectedSkills.includes(skill.id)
                                                                                    ? 'bg-[#000080] text-white shadow-sm'
                                                                                    : 'bg-white hover:bg-gray-50 text-gray-900 border border-gray-200'
                                                                                }`}
                                                                            >
                                                <span className="truncate flex-1 text-[14px] font-medium">
                                                    {skill.skill_name_heb}
                                                </span>
                                                </button>
                                            ))}
                                    </div>

                                    {/* Clear Skills Button */}
                                {selectedSkills.length > 0 && (
                                    <div className="px-4 py-4 border-t border-gray-200">
                                        <button
                                            onClick={() => setSelectedSkills([])}
                                            className="w-full py-2 px-4 text-[#ff7f50] hover:bg-orange-50 rounded-lg text-[14px] font-medium transition-colors mx-2"
                                        >
                                            נקה סינון מיומנויות
                                        </button>
                                    </div>
                                )}
                            </div>
                        </div>
                        {/* Cards Grid */}
                        <div className="flex-1">
                            <div className="grid gap-[30px] w-full" style={{
                                display: 'grid',
                                gridTemplateColumns: 'repeat(auto-fill, 320px)',
                                justifyContent: 'start',
                                gap: '30px',
                                width: '100%',
                                maxWidth: '100%',
                                position: 'relative'
                            }}>
                                {loading ? (
                                    <div className="col-span-full flex justify-center py-4">
                                        <div className="flex space-x-2 items-center">
                                            <div className="w-2 h-2 bg-primary rounded-full animate-bounce"
                                                 style={{animationDelay: '0ms'}}/>
                                            <div className="w-2 h-2 bg-primary rounded-full animate-bounce"
                                                 style={{animationDelay: '150ms'}}/>
                                            <div className="w-2 h-2 bg-primary rounded-full animate-bounce"
                                                 style={{animationDelay: '300ms'}}/>
                                            </div>
                                        </div>
                                    ) : error ? (
                                        <div className="text-red-500">{error}</div>
                                    ) : (
                                        cards.map((card) => (
                                            <div key={card.id} style={{
                                                width: '320px',
                                                justifySelf: 'center',
                                                minHeight: '250px'
                                            }}>
                                                <EducationCard
                                                    title={card.title_heb}
                                                    skills={card.skills.map(skill => ({
                                                        ...skill,
                                                        skill_name_eng: skill.skill_name_heb // Use Hebrew names
                                                    }))}
                                                    grades={card.grades.map(grade => ({
                                                        ...grade,
                                                        name_eng: grade.name_heb // Use Hebrew names
                                                    }))}
                                                    requiresLogin={card.requiresLogin}
                                                    url={card.url ? (card.url.startsWith('http') ? card.url : `/portal${card.url}`) : null}
                                                    subject={card.subject}
                                                    colorDark={card.colorDark}
                                                    isHebrew={true}
                                                />
                                            </div>
                                        ))
                                    )}

                                    {!loading && !error && cards.length === 0 && (
                                        <div
                                            className="col-span-full flex flex-col items-center justify-center py-12 px-4">
                                            <div className="text-gray-500 text-lg mb-4">
                                                לא נמצאו כרטיסים לפי הקריטריונים שנבחרו
                                            </div>
                                            <button
                                                onClick={clearFilters}
                                                className="px-6 py-2 bg-primary text-white rounded-lg hover:bg-opacity-90 transition-colors flex items-center space-x-2"
                                            >
                                                <svg className="w-5 h-5" fill="none" stroke="currentColor"
                                                     viewBox="0 0 24 24">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2}
                                                          d="M6 18L18 6M6 6l12 12"/>
                                                </svg>
                                                <span>נקה את כל הסינונים</span>
                                            </button>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </main>
            </div>
        </div>
    );
};

export default HebPortalDashboard;