class AuthService {
    constructor() {
        this._cachedLoginState = null;
        this._lastCheck = 0;
        this._isInitialCheck = true;
    }

    async isLoggedIn() {
        // Use cached value if recent (within 5 seconds)
        const currentTime = Date.now();
        if (this._cachedLoginState && currentTime - this._lastCheck < 5000) {
            return this._cachedLoginState;
        }

        try {
            const response = await fetch('/auth/verify-token', {
                method: 'GET',
                credentials: 'include',
                headers: {
                    'Accept': 'application/json',
                    'X-Requested-With': 'XMLHttpRequest'
                }
            });

            if (!response.ok) {
                if (response.status === 401) {
                    this._cacheLoginState([false, null], currentTime);
                    return [false, null];
                }
                console.log('Verify token failed:', response.status);
                this._cacheLoginState([false, null], currentTime);
                return [false, null];
            }

            const data = await response.json();
            this._isInitialCheck = false;

            if (data.valid) {
                this._cacheLoginState([true, data.user_id], currentTime);
                return [true, data.user_id];
            } else {
                this._cacheLoginState([false, null], currentTime);
                return [false, null];
            }
        } catch (error) {
            console.error("Error checking login status:", error);
            this._cacheLoginState([false, null], currentTime);
            return [false, null];
        }
    }

    async logout() {
        try {
            const response = await fetch('/auth/logout', {
                method: 'POST',
                credentials: 'include',
                headers: {
                    'Accept': 'application/json',
                    'X-Requested-With': 'XMLHttpRequest'
                }
            });

            if (!response.ok) {
                throw new Error('Logout failed');
            }

            // Clear cached state
            this._cachedLoginState = null;
            this._lastCheck = 0;
            this._isInitialCheck = true;

            // Return success without redirecting
            return true;
        } catch (error) {
            console.error("Logout error:", error);
            throw error;
        }
    }

    _cacheLoginState(state, timestamp) {
        this._cachedLoginState = state;
        this._lastCheck = timestamp;
    }
}

export const Auth = new AuthService();